<template>
  <div class="hello">
    <el-form ref="form" :model="form" label-width="80px">
      <el-row>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label="合同号">
              <el-input v-model="form.contractNum"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label="文件名">
              <el-input v-model="form.contractName"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label="目录名">
              <el-input v-model="form.directory"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
          <el-form-item label="资方" class="select-align">
            <el-select v-model="form.capital" placeholder="请选择资方">
              <el-option label="资方1" value="1"></el-option>
              <el-option label="资方2" value="2"></el-option>
              <el-option label="资方3" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>

        <el-col :span="6">
          <el-form-item label="开始时间">
            <el-date-picker type="date" placeholder="选择日期" v-model="form.signDateStart" style="width: 100%;"
                            value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="结束时间">
            <el-date-picker type="date" placeholder="选择日期" v-model="form.signDateEnd" style="width: 100%;"
                            value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label=" 身份证号">
              <el-input v-model="form.identityCardNum"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
          <el-form-item label="合同状态" class="select-align">
            <el-select v-model="form.contractState" placeholder="请选择合同状态">
              <el-option label="初始化" value="1"></el-option>
              <el-option label="进行中" value="2"></el-option>
              <el-option label="已结清" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label="客户姓名">
              <el-input v-model="form.clientName"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label="手机号">
              <el-input v-model="form.clientMobile"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label="客户编号">
              <el-input v-model="form.clientNum"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content select-align">
            <el-form-item label="合同类型">
              <el-select v-model="form.contractStatus" placeholder="请选择合同类型">
                <el-option label="类型1" value="1"></el-option>
                <el-option label="类型2" value="2"></el-option>
                <el-option label="类型3" value="3"></el-option>
              </el-select>
            </el-form-item>
          </div>
        </el-col>


      </el-row>

      <el-form-item>
        <el-button @click="exportExcel">导出当前记录为excel</el-button>
        <el-button @click="exportContract">导出对应合同文件</el-button>
        <el-button @click="getData"> 查询</el-button>
      </el-form-item>
      <el-table
        v-loading="loading"
        :data="tableData"
        border
        style="width: 100%;text-align: left">
        <el-table-column v-for="item in tableIndex"
                         v-bind:label="item.value"
                         v-bind:prop="item.key">
        </el-table-column>
      </el-table>
      <el-pagination
        v-show="response"
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="form.per_page"
        :total="form.totalCnt"
        :current-page.sync="form.current_page"
      >
      </el-pagination>
    </el-form>
  </div>
</template>

<script>

  export default {
    name: 'Contract',
    data() {
      return {
        response: false,
        form: {
          contractId: "",//合同id
          contractNum: "",//合同号
          directory: "",//目录名
          clientMobile: "",//手机号
          clientNum: "",//用户id
          capital: "",//资方
          contractStatu: "",//合同状态
          contractName: "",//文件名
          signDate: "",//签署时间
          identityCardNum: "",//身份证号
          signDateStart: "", // 开始时间
          signDateEnd: "", // 结束时间
          clientName: "", //客户姓名
          per_page: 0,//每页数量
          current_page: 1,//当前页
          totalCnt: 0,//一共多少条
          s3Bucket: null,
          s3Key: null,
          operator: null,
          del: 0
        },
        loading: false,
        tableIndex: [
          {key: 'contractNum', value: '合同号'},
          {key: 'clientName', value: '客户姓名'},
          {key: 'clientMobile', value: '客户手机号'},
          {key: 'clientNum', value: '客户编号'},
          {key: 'capital', value: '资方'},
          {key: 'contractState', value: '合同状态'},
          {key: 'contractName', value: '文件名'},
          {key: 'directory', value: '目录名'},
          {key: 'signDate', value: '合同签署时间'},
          {key: 'identityCardNum', value: '身份证号'}],
        tableData: []
      }
    },
    methods: {
      handleCurrentChange() {
        //page change
        this.getData()
      },
      getData() {
        this.loading = true;
        let data = this.form;
		console.log(data);
		data.signDateStart += data.signDateStart.length == 0 ? '' : ' 00:00:00';
		data.signDateEnd += data.signDateEnd.length == 0 ? '' : ' 23:59:59'
		console.log(data);
        this.$ajax('post', 'http://localhost:8080/contract/search', data, res => {
          this.loading = false
          if (res != '' && res.data != undefined) {
            this.response = true;
            console.log(res);
            res.data.contractStatus = res.data.contractStatus + ''
            this.tableData = res.data;
            this.form.current_page = res.data.pageIndex;
            this.form.per_page = res.data.pageSize;
            this.form.totalCnt = res.data.pageTotal;

          } else {
            this.loading = false
            this.tableData = []
            console.error('接口数据错误')
          }
        }, res => {
          this.loading = false
          console.log(res)
        })
      },
      exportExcel() {
        let data = this.form;
		data.signDateStart += data.signDateStart.length == 0 ? '' : ' 00:00:00';
		data.signDateEnd += data.signDateEnd.length == 0 ? '' : ' 23:59:59'
        this.$ajax('post', 'http://localhost:8080/contract/excel/export', data, res => {
          if (res != '' && res.code == 200) {
            console.log(res)
            this.$message('导出成功');
          } else {
            console.error('接口数据错误')
          }
        }, res => {
          console.log(res)
        })
      },
      exportContract() {
        let data = this.form;
		data.signDateStart += data.signDateStart.length == 0 ? '' : ' 00:00:00';
		data.signDateEnd += data.signDateEnd.length == 0 ? '' : ' 23:59:59'
        this.$ajax('post', 'http://localhost:8080/contract/zip', data, res => {
          if (res != '' && res.code==200) {
            this.$message('任务提交成功，后续请留意邮件');
          }
        }, res => {
          console.log(res)
        })
      }
    /*  exportContractByExcel() {
        this.$ajax('post', 'http://localhost:8080/contract/search', data, res => {
          if (res != '' && res.data != undefined) {
            console.log(res)
          } else {
            console.error('接口数据错误')
          }
        }, res => {
          console.log(res)
        })
      }*/
    },
    mounted() {

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .select-align {
    text-align: left;
  }


</style>



